<script>
import "swiper/css/swiper.css";
import Swiper from "swiper";
export default {
  name: "catgpt",
  data() {
    return {
      nameid: "",
      emailid: "",
      opinion: "",
      menu_box: false,
    };
  },
  mounted() {
    this.customers();
    this.showhide_header();
    this.color_header();
  },
  methods: {
    menuclose() {
      this.menu_box = false;
    },
    menuopen() {
      this.menu_box = true;
    },
    color_header() {
      window.addEventListener(
        "scroll",
        function () {
          var navbar = document.querySelector(".header__wrapper");
          var scrollPosition =
            window.pageYOffset ||
            document.documentElement.scrollTop ||
            document.body.scrollTop;
          var number = 0;
          if (window.screen.width < 1024) {
            number = 50;
          } else {
            number = 200;
          }
          if (scrollPosition <= number) {
            navbar.classList = "header_top header__wrapper acvi";
          } else {
            navbar.classList = "header_top header__wrapper erro";
          }
        },
        true
      );
    },
    showhide_header() {
      var header = document.querySelector(".header");
      console.log(header);
      var prevScrollPos = window.pageYOffset;
      window.addEventListener(
        "scroll",
        function () {
          var currentScrollPos =
            window.pageYOffset ||
            document.documentElement.scrollTop ||
            document.body.scrollTop;
          if (prevScrollPos > currentScrollPos) {
            header.classList.remove("headeractive");
            header.classList.add("headernone");
          } else {
            header.classList.add("headeractive");
            header.classList.remove("headernone");
          }
          prevScrollPos = currentScrollPos;
        },
        true
      );
    },
    customers() {
      var mySwiper = new Swiper("#swiperCus", {
        //
        direction: "horizontal",
        mousewheel: false,
        centeredSlides: true,
        slidesPerView: 1,
        allowTouchMove: true,
        observer: true,
        observeParents: true,
        spaceBetween: 0,
        loop: true,
        pagination: {
          el: ".swiper-pagination",
          clickable: true,
        }, //
        autoplay: {
          delay: 3000,
          disableOnInteraction: false,
        },
        // autoplay: false,
        // 
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev",
        },
      });
      mySwiper.el.onmouseover = function () {
        mySwiper.autoplay.stop();
      };
      mySwiper.el.onmouseout = function () {
        mySwiper.autoplay.start();
      };
    },
    wedoedClick(e) {
      this.menu_box = false;
      document.querySelector(e).scrollIntoView({
        behavior: "smooth",
        block: "center",
        inline: "nearest",
      });
    },
  },
};
</script>
